import React from 'react';
import styles from './Section04.scss'
import classNames from 'classnames';

const cx = classNames.bind(styles);

class Section04 extends React.Component {
    render() {
        return (
            <div className={cx(styles['fourth-section'], 'section')}>
                <div className={styles['fourth-section--desc']}>
                    <div className={styles['fourth-section--integration']}>
                        <h2>APP INTEGRATION</h2>
                    </div>
                    <h1>기록부터 알림까지
                        앱으로 받아보세요.</h1>
                </div>
                <div className={styles['fourth-section--spec']}>
                    <div className={styles['fourth-section--spec--box']}>
                        <img src={require('./web-peepee-4-icon-1.svg')} alt={'기저귀 상태  - 실시간 기저귀의 습도 및 온도를 보여줍니다.'}/>
                        <div className={styles['fourth-section--spec--box--txt']}>
                            <h3>기저귀 상태</h3>
                            <p>실시간 기저귀의<br/>
                                습도 및 온도를 보여줍니다.</p>
                        </div>
                    </div>
                    <div className={styles['fourth-section--spec--box']}>
                        <img src={require('./web-peepee-4-icon-2.svg')} alt={'배변 알림 - 배변시 앱을 통해 알려주고 배변일지를 작성합니다.'}/>
                        <div className={styles['fourth-section--spec--box--txt']}>
                            <h3>배변 알림</h3>
                            <p>배변시 앱을 통해 알려주고<br/>
                                배변일지를 작성합니다.</p>
                        </div>
                    </div>
                    <div className={styles['fourth-section--spec--box']}>
                        <img src={require('./web-peepee-4-icon-3.svg')} alt={'기저귀 소비량 확인 - 기저귀 교체를 감지해 다음 번 기저귀 주문 시점을 예측할 수 있습니다.'}/>
                        <div className={styles['fourth-section--spec--box--txt']}>
                            <h3>기저귀 소비량 확인</h3>
                            <p>기저귀 교체를 감지해 다음 번<br/>
                                기저귀 주문 시점을 예측할 수 있습니다.</p>
                        </div>
                    </div>
                </div>
                <div className={styles['fourth-section--app-image']}></div>
            </div>
        )
    }
}

export default Section04;
